(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .controller('msSbAddCardController', msSbAddCardController)
        .directive('msSbAddCard', msSbAddCardDirective);

    /** @ngInject */
    function msSbAddCardController($scope, $timeout, BoardService, $mdDialog, $document, msUtils)
    {
        var vm = this;

        vm.newCardName = '';
        vm.listId = $scope.msListId;
        vm.board = BoardService.data.data;
        vm.cards = vm.board.cards;
        vm.list = vm.board.lists.getById(vm.listId);

        // Methods
        vm.addNewCard = addNewCard;

        /////

        vm.addIssue = function(ev) {

                console.log(vm.list.name);
          $mdDialog.show({
              controller         : 'CreateIssueController',
              controllerAs       : 'vm',
              locals             : {
                  board: vm.board,
                  name: vm.list.name
              },
              templateUrl        : 'app/main/issues/dialogs/createIssue/createIssue.html',
              parent             : angular.element($document.body),
              targetEvent        : ev,
              clickOutsideToClose: true
          });
        }

        /**
         * Add New Card
         */
        function addNewCard()
        {
            if ( vm.newCardName === '' )
            {
                return;
            }

            var newCardId = msUtils.guidGenerator();

            vm.cards.push({
                id               : newCardId,
                name             : vm.newCardName,
                description      : '',
                idAttachmentCover: '',
                idMembers        : [],
                idLabels         : [],
                attachments      : [],
                subscribed       : false,
                checklists       : [],
                checkItems       : 0,
                checkItemsChecked: 0,
                comments         : [],
                activities       : [],
                due              : null
            });

            vm.list.idCards.push(newCardId);

            $timeout(function ()
            {
                $scope.scrollListContentBottom();
            });

            vm.newCardName = '';
        }
    }

    /** @ngInject */
    function msSbAddCardDirective($document, $window, $timeout)
    {
        return {
            restrict   : 'E',
            controller : 'msSbAddCardController as vm',
            templateUrl: 'app/main/issues/directives/ms-sb-add-card/ms-sb-add-card.html',
            scope      : {
                msListId: '='
            },
            link       : function (scope, iElement)
            {
                scope.formActive = false;
                scope.toggleForm = toggleForm;
                scope.scrollListContentBottom = scrollListContentBottom;

                var buttonEl = iElement.find('.ms-sb-add-card-button'),
                    formEl = iElement.find('.ms-sb-add-card-form'),
                    listCards = iElement.parent().prev().find('.list-cards');

                /**
                 * Click Event
                 */
                buttonEl.on('click', toggleForm);

                /**
                 * Toggle Form
                 */
                function toggleForm()
                {
                    scope.$evalAsync(function ()
                    {
                        scope.formActive = !scope.formActive;

                        if ( scope.formActive )
                        {
                            $timeout(function ()
                            {
                                formEl.find('input').focus();

                                scrollListContentBottom();
                            });

                            $document.on('click', outSideClick);
                        }
                        else
                        {
                            PerfectScrollbar.update(listCards[0]);
                            $document.off('click', outSideClick);
                        }

                        $timeout(function ()
                        {
                            // IE list-content max-height hack
                            if ( angular.element('html').hasClass('explorer') )
                            {
                                angular.element($window).trigger('resize');
                            }
                        });

                    });
                }

                /**
                 * Scroll List to the Bottom
                 */
                function scrollListContentBottom()
                {
                    listCards[0].scrollTop = listCards[0].scrollHeight;
                }

                /**
                 * Click Outside Event Handler
                 * @param event
                 */
                var outSideClick = function (event)
                {
                    var isChild = formEl.has(event.target).length > 0;
                    var isSelf = formEl[0] === event.target;
                    var isInside = isChild || isSelf;

                    if ( !isInside )
                    {
                        toggleForm();
                    }
                };
            }
        };
    }
})();

(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .controller('WordCloudController', WordCloudController);

    /** @ngInject */
    function WordCloudController($mdDialog, issue, wordCloud, $timeout) {

      var vm = this;

      vm.closeDialog = closeDialog;

      function closeDialog()
        {
          $mdDialog.hide();
        }

      $timeout(function () {
          wordCloud.drawWordCloud(createWordArray());
      }, 100);


      // gets all the comments and issue description and converts them to word array
      function createWordArray() {

          var wordCloudText = "";
          for (var i = 0; i < issue.comments.length; ++i) {
              wordCloudText += " " + issue.comments[i].commentText;
          }

          // adding description to the word cloud
          wordCloudText += " " + issue.description;

          // adding cheklist items name to te word cloud
          angular.forEach(issue.checklists, function(v, k) {
            angular.forEach(v.checkItems, function(item, key) {
              wordCloudText += " " + item.name;
            });
          });


          return wordCloudText.split(" ");

      }

    }

})();

(function ()
{
    'use strict';

    angular
        .module('app.issues')
        .controller('CreateIssueController', CreateIssueController);

    /** @ngInject */
    function CreateIssueController($mdDialog, apilaData, board, name, authentication, msNavigationService) {

      var vm = this;

        var username = authentication.currentUser().name;

      //Functions
      vm.closeDialog = closeDialog;
      vm.addIssue = addIssue;

      if(name != null) {
        vm.selectedItem = {value: name,
                           display: name
                             };
      }

      apilaData.userCommunity(username)
      .success(function(d) {
        vm.myCommunity = d;
      });

      function closeDialog()
      {
          $mdDialog.hide();
      }

      vm.getMatches = function (text) {
        if(text === null) {
          return;
        }
        var textLower = text.toLowerCase();

          var ret = vm.residentList.filter(function (d) {
              if(d.display != null)
              return d.display.toLowerCase().indexOf(text) > -1;
          });
          return ret;
      }


     vm.residentList = [];
     vm.selectedUser = {};

     apilaData.usersList()
       .success(function(usersList) {
         vm.residentList = usersList.map(function(elem) {
           return {value: elem.name, display: elem.name};
         });
       })
       .error(function(usersList) {
         console.log("Error retriving the list of residents");
       });

      function addIssue() {

        vm.form.responsibleParty = vm.selectedItem.value;
        vm.form.community = vm.myCommunity;

        apilaData.addIssue(vm.form)
            .success(function(issue) {

              issue.id = issue._id;
              issue.name = issue.title;

              if(board.data === undefined) {
                board.data = board;
              }

              board.data.cards.push(issue);

              var username = authentication.currentUser().name;

              apilaData.openIssuesCount(username)
                .success(function(count) {
                  msNavigationService.saveItem('fuse.issues', {
                    badge: {
                      content:  count,
                      color  : '#F44336'
                    }
                  });
                })
                .error(function(count) {
                })


              for(var i = 0; i < board.data.lists.length;++i) {
                if(board.data.lists[i].name === issue.responsibleParty) {
                  board.data.lists[i].idCards.push(issue.id);
                  break;
                }

              }


              closeDialog();
            })
            .error(function(issue) {
                console.log("Error while adding issue");
            });
      }

      function findListByName(name, id) {
        angular.forEach(board.data.lists, function(v, k) {
          if(v.name === name){
            v.idCards.push(id);
            return;
          }
        });
      }

    }

})();

