(function ()
{
    'use strict';

    angular
        .module('app.dashboard')
        .controller('UpdateBillingController', UpdateBillingController);

    /** @ngInject */
    function UpdateBillingController($mdDialog, apilaData, authentication, $mdToast) {

      var vm = this;

      vm.closeDialog = closeDialog;
      vm.updateCustomer = updateCustomer;

      vm.userid = authentication.currentUser().id;

      function closeDialog()
      {
          $mdDialog.hide();
      }

      function updateCustomer() {
        Stripe.card.createToken(vm.cardInfo,
          function(status, response) {

            console.log(response);

            if(status !== 200) {
              showErrorToast(response.error.message);
            } else {
              apilaData.updateCustomer(vm.userid, response)
              .success(function(response) {
                console.log(response);
                closeDialog();
              })
              .error(function(response) {
                console.log(response);
              });
            }



          });
      }

      function showErrorToast(errorMsg) {
        $mdToast.show(
          $mdToast.simple()
            .textContent(errorMsg)
            .position("top right")
            .hideDelay(3000)
        );
      }

    }

})();

(function ()
{
    'use strict';

    angular
        .module('app.dashboard')
        .controller('RecoverController', RecoverController);

    /** @ngInject */
    function RecoverController($mdDialog, recoveryInfo, apilaData, $mdToast) {

      var vm = this;

      // Data
      vm.recoveryInfo = recoveryInfo;

      // Functions
      vm.closeDialog = closeDialog;
      vm.verifyPassword = verifyPassword;

      function closeDialog()
      {
          $mdDialog.hide();
      }

      function verifyPassword() {

        var data = {};
        data.password = vm.form.password;
        data.recoveryid = vm.recoveryInfo.recoveryid;
        data.type = vm.recoveryInfo.type;

        apilaData.verifyPassword(data, vm.recoveryInfo.bossId)
        .success(function(response) {
          closeDialog();

          $mdToast.show(
            $mdToast.simple()
              .textContent("Your password has been verified")
              .position("top right")
              .hideDelay(2000)
          );
        })
        .error(function(response) {

          console.log(response);

          $mdToast.show(
            $mdToast.simple()
              .textContent("Your password doesn't match!")
              .position("top right")
              .hideDelay(2000)
          );
        });
      }

    }

})();

(function ()
{
    'use strict';

    angular
        .module('app.dashboard')
        .controller('JoinCommunityController', JoinCommunityController);

    /** @ngInject */
    function JoinCommunityController($mdDialog, apilaData, $mdToast, authentication) {

      var vm = this;

      vm.searchText = "";

      // Data
      vm.username = authentication.currentUser().name;

      // Functions
      vm.closeDialog = closeDialog;
      vm.sendRequest = sendRequest;

      apilaData.communityList()
        .success(function(communityList) {
          //console.log(communityList);
          vm.communityList = communityList.map(function(elem) {
            return {value: elem._id, display: elem.name};
          });
        })
        .error(function(communityList) {
          console.log("Error retriving the list of residents");
        });


        vm.getMatches = function (text) {
          if(text === null) {
            return vm.communityList;
          }
          var textLower = text.toLowerCase();

            var ret = vm.communityList.filter(function (d) {
                if(d.display != null)
                return d.display.toLowerCase().indexOf(text) > -1;
            });
            return ret;
        }

      function closeDialog()
      {
          $mdDialog.hide();
      }

      function sendRequest()
      {

        if(vm.selectedItem === null) {
          return;
        }

        $mdToast.show(
          $mdToast.simple()
            .textContent('Invite Request Sent to ' + vm.selectedItem.display)
            .position("top right")
            .hideDelay(3000)
        );

        vm.communityId = vm.selectedItem.value;

        var data = {};
        data.pendingMember = vm.username;

        apilaData.addPendingMember(data, vm.communityId)
        .success(function(d) {
          vm.searchText = "";
          closeDialog();
        })
        .error(function(d) {
          closeDialog();
        });
      }


    }

  })();

(function ()
{
    'use strict';

    angular
        .module('app.dashboard')
        .controller('CreateCommunityController', CreateCommunityController);

    /** @ngInject */
    function CreateCommunityController($mdDialog, apilaData, authentication, $mdToast) {

      var vm = this;

      //Functions
      vm.closeDialog = closeDialog;
      vm.addCommunity = addCommunity;
      vm.recoverCommunity = recoverCommunity;

      // Data
      vm.form = {};

      vm.hasCanceledCommunity = false;

      vm.username = authentication.currentUser().name;
      vm.userid = authentication.currentUser().id;

      vm.restoreCommunity = false;

      apilaData.hasCanceledCommunity(vm.userid)
      .success(function(response) {
        vm.canceledCommunity = response;
        vm.hasCanceledCommunity = true;
      })
      .error(function(response) {
      });

      function closeDialog()
      {
          $mdDialog.hide();
      }


      function showErrorToast(errorMsg) {
        $mdToast.show(
          $mdToast.simple()
            .textContent(errorMsg)
            .position("top right")
            .hideDelay(3000)
        );
      }

      function addCommunity()
      {
        vm.form.communityMembers = [];
        vm.form.pendingMembers = [];

        vm.form.username = vm.username;

        console.log(vm.cardInfo);
        console.log(vm.userid);

        if(!vm.form.name) {
          vm.nameError = "Community name must be specified";
          return;
        }

        Stripe.card.createToken(vm.cardInfo,
          function(status, response) {

            console.log(response);

            if(status !== 200) {
            //  showErrorToast(response.error.message);
              processErrors(response.error);
            } else {
              apilaData.saveCreditCard(vm.userid, response)
              .success(function(response) {
                console.log(response);

                apilaData.addCommunity(vm.form)
                .success(function(d) {
                  closeDialog();

                  $mdToast.show(
                    $mdToast.simple()
                      .textContent("Community has been created!")
                      .position("top right")
                      .hideDelay(2200)
                  );
                })
                .error(function(d) {
                  console.log("Error while creating community");
                });
              })
              .error(function(response) {
                console.log(response);
              });
            }


          });

      }


      function processErrors(error) {
        if(error.param === "card[number]") {
          vm.cardNumberError = "Invalid credit card number";
        } else if(error.param === "card[exp_month]") {
          vm.expMonthError = "Invalid Expiration Month entered please select between 01 - 12";
        } else if(error.param === "card[exp_year]") {
          vm.expYearError = "Invalid Expiration Year entered";
        }

      }

      function recoverCommunity() {
        Stripe.card.createToken(vm.cardInfo,
          function(status, response) {

            console.log(response);

            if(status !== 200) {
              //showErrorToast(response.error.message);
              processErrors(response.error);
            } else {
              apilaData.saveCreditCard(vm.userid, response)
              .success(function(response) {
                console.log(response);

                //restore community
                apilaData.restoreCommunity(vm.userid, vm.canceledCommunity._id)
                .success(function(response) {
                  console.log(response);

                  closeDialog();

                  $mdToast.show(
                    $mdToast.simple()
                      .textContent("Community has been restored!")
                      .position("top right")
                      .hideDelay(2200)
                  );

                })
                .error(function(response) {
                  console.log(response);
                });
              })
              .error(function(response) {

              });
            }

          });
      }




  }

})();

