(function() {
  'use strict';

  angular
    .module('app.appointments')
    .controller('AppoitmentsController', AppoitmentsController);

  /** @ngInject */
  function AppoitmentsController($mdDialog, $document, apilaData, msNavigationService,
                                 authentication, exportPdf, $state, $scope, SearchService) {
    var vm = this;

    // Data
    var date = new Date();
    var d = date.getDate();
    var m = date.getMonth();
    var y = date.getFullYear();

    var appointList = [];

    var appointments = null;

    var username = authentication.currentUser().name;


    function openIssuesCount(id) {
      apilaData.openIssuesCount(username, id)
        .success(function(count) {
          msNavigationService.saveItem('fuse.issues', {
            badge: {
              content: count,
              color: '#F44336'
            }
          });
        })
        .error(function(count) {});
    }


    apilaData.userCommunity(username)
      .success(function(d) {
        vm.community = d;
        loadAppoitnments(vm.community._id);
        openIssuesCount(vm.community._id);
        loadIssues(vm.community._id);
        loadBirthdays(vm.community._id);

        var searchParams = ["title", "transportation", "locationDoctor", "reason", "locationName",
                            "cancel", "appointmentDate"];

        SearchService.setData(appointList, searchParams);

        SearchService.subscribe($scope, function() {

          vm.events[0] = SearchService.getResult();

          console.log(vm.events[0]);
        });

      });

    var loadAppoitnments = function(id) {

      //load all the events and show them on the callendar
      apilaData.appointmentsList(id)
        .success(function(data) {
          appointments = data;
          var i = 1;
          angular.forEach(data, function(value, key) {
            var currAppoint = addAppointment(value);
            appointList.push(currAppoint);

          });

          vm.events[0] = appointList;

        })
        .error(function(e) {
          console.log("error loading appointments");
        });

    };

    var loadIssues = function(id) {
      apilaData.dueIssuesList(id)
      .success(function(response) {
        angular.forEach(response, function(value, key) {

          var dueDate =  new Date(value.due);

          var calEvent = {
            title: value.title,
            start: dueDate,
            end: null,
            stick: true,
            color: "#228B22"
          };

          appointList.push(calEvent);

        });
      })
      .error(function(response) {

      });
    }

    var loadBirthdays = function(id) {
      apilaData.residentBirthday(id)
      .success(function(response) {
        angular.forEach(response, function(value, key) {

          // get current year, and set that for birthday on cal
          var currYear = moment().year();
          var startDate = moment(value.birthDate).year(currYear);

          var calEvent = {
            title: value.firstName + " " + value.lastName + "'s Birthday",
            start: startDate,
            end: null,
            stick: true,
            color: "#9C27B0"
          };

          appointList.push(calEvent);

        });
      })
      .error(function(response) {

      });
    }


    vm.events = [
      []
    ];

    vm.calendarUiConfig = {
      calendar: {
        editable: true,
        eventLimit: false,
        header: '',
        handleWindowResize: false,
        aspectRatio: 1,
        dayNames: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
        dayNamesShort: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
        viewRender: function(view) {
          console.log(view);

          if(view.name === "agendaDay") {
            //$document.scrollTop(1000, 1000);;
          }

          vm.calendarView = view;
          vm.calendar = vm.calendarView.calendar;
          vm.currentMonthShort = vm.calendar.getDate().format('MMM');



        },
        columnFormat: {
          month: 'ddd',
          week: 'ddd D',
          day: 'ddd M'
        },
        eventClick: editEvent,
        selectable: true,
        selectHelper: true,
        select: select
      }
    };

    // Methods
    vm.addEvent = addEvent;
    vm.next = next;
    vm.prev = prev;
    vm.exportAppointments = exportAppointments;

    //////////

    function exportAppointments() {
      var columns = ["Resident", "Date", "Reason", "Location", "Doctor", "Transportation"];

      var month = vm.calendar.getDate().format("MMMM");

      var sortedEvents = _.sortBy(vm.events[0], function(p) {
        return p.date;
      });

      var rows = _.map(_.filter(sortedEvents, function(o) {
        return moment(o.start).format("MM") === vm.calendar.getDate().format("MM") && o.cancel === false;
      }), function(d) {
        var arr = [];

        var name = "";
        if (d.residentGoing != null) {
          name = d.residentGoing.firstName + " " + d.residentGoing.lastName;
        }

        console.log(d.date);
        console.log(moment.utc(d.date).format("MM/DD hh:mm A"));

        arr.push(name);
        arr.push(moment.utc(d.date).format("MM/DD hh:mm A"));
        arr.push(d.reason);
        arr.push(d.locationName);
        arr.push(d.locationDoctor);
        arr.push(d.transportation);

        return arr;
      });

      var doc = new jsPDF('p', 'pt');
      doc.autoTable(columns, rows, {
        beforePageContent: function(data) {
          doc.text("Appointments for " + month, 170, 30);
        }
      });
      doc.save(month + '-Appointments.pdf');
    }

    function next() {
      vm.calendarView.calendar.next();
    }

    function prev() {
      vm.calendarView.calendar.prev();
    }

    function eventDetail(calendarEvent, e) {
      showEventDetailDialog(calendarEvent, e);
    }

    function select(start, end, e) {
      showEventFormDialog('add', false, start, end, e);
    }

    function addEvent(e) {
      var start = new Date().getUTCDate(),
        end = new Date();

      showEventFormDialog('add', false, start, end, e);
    }


    function showEventDetailDialog(calendarEvent, e) {
      $mdDialog.show({
        controller: 'EventDetailDialogController',
        controllerAs: 'vm',
        templateUrl: 'app/main/appointments/dialogs/event-detail/event-detail-dialog.html',
        parent: angular.element($document.body),
        targetEvent: e,
        clickOutsideToClose: true,
        locals: {
          calendarEvent: calendarEvent,
          showEventFormDialog: showEventFormDialog,
          event: e
        }
      });
    }

    function editEvent(calendarEvent)
    {
        showEventFormDialog('edit', calendarEvent, false, false, event);
    }

    function addAppointment(appointemnt) {
      return createAppointemnt(appointemnt, "add");
    }

    function updateAppointment(appointemnt) {
      return createAppointemnt(appointemnt, "update");
    }

    function createAppointemnt(appointment, type) {
      var hours = appointment.hours;

      if (!appointment.isAm) {
        appointment.hours += 12;
      }

      //if it's 12 am we need it to 00 for the ISO format thing
      if(appointment.hours == 12 && appointment.isAm) {
        appointment.hours = 0;
      }

      //if it's 12 pm it will be 24 but we need to set 12 in ISO format
      if(appointment.hours == 24) {
        appointment.hours = 12;
      }

      var startDate = new Date(appointment.appointmentDate);
      startDate.setHours(appointment.hours);
      startDate.setMinutes(appointment.minutes);

      var calEvent = {
        title: appointment.residentGoing.firstName + " " + appointment.residentGoing.lastName +
          " to " + appointment.locationName,
        start: startDate,
        end: null,
        transportation: appointment.transportation,
        reason: appointment.reason,
        isAm: appointment.isAm,
        minutes: appointment.minutes,
        hours: hours,
        timezone: appointment.timezone,
        locationDoctor: appointment.locationDoctor,
        locationName: appointment.locationName,
        date: appointment.time,
        currentUser: appointment.residentGoing,
        appointId: appointment._id,
        cancel: appointment.cancel,
        appointmentComment: appointment.appointmentComment,
        residentGoing: appointment.residentGoing,
        stick: true,
      }

      if(type === "add") {
        calEvent.id = appointment._id;
        calEvent._id = appointment._id;
      }

      if (appointment.cancel === true) {
        calEvent.color = "#f24438";
      }

      return calEvent;
    }

    function showEventFormDialog(type, calendarEvent, start, end, e) {
      var dialogData = {
        type: type,
        calendarEvent: calendarEvent,
        start: start,
        end: end
      };

      $mdDialog.show({
        controller: 'EventFormDialogController',
        controllerAs: 'vm',
        templateUrl: 'app/main/appointments/dialogs/event-form/event-form-dialog.html',
        parent: angular.element($document.body),
        targetEvent: e,
        clickOutsideToClose: true,
        locals: {
          dialogData: dialogData
        }
      }).then(function(response) {

        if (response.type === 'add') {
          vm.events[0].push(addAppointment(response.calendarEvent));

        } else {

          for (var i = 0; i < vm.events[0].length; i++) {

            // Update
            if (vm.events[0][i].appointId == response.calendarEvent.appointId) {

              vm.events[0][i] = updateAppointment(response.calendarEvent);
              break;
            }
          }
        }
      });
    }

  }

})();

(function ()
{
    'use strict';

    angular
        .module('app.core')
        .service('fuseTheming', fuseThemingService);

    /** @ngInject */
    function fuseThemingService($cookies, $log, $mdTheming)
    {
        var service = {
            getRegisteredPalettes: getRegisteredPalettes,
            getRegisteredThemes  : getRegisteredThemes,
            setActiveTheme       : setActiveTheme,
            setThemesList        : setThemesList,
            themes               : {
                list  : {},
                active: {
                    'name' : '',
                    'theme': {}
                }
            }
        };

        return service;

        //////////

        /**
         * Get registered palettes
         *
         * @returns {*}
         */
        function getRegisteredPalettes()
        {
            return $mdTheming.PALETTES;
        }

        /**
         * Get registered themes
         *
         * @returns {*}
         */
        function getRegisteredThemes()
        {
            return $mdTheming.THEMES;
        }

        /**
         * Set active theme
         *
         * @param themeName
         */
        function setActiveTheme(themeName)
        {
            // If theme does not exist, fallback to the default theme
            if ( angular.isUndefined(service.themes.list[themeName]) )
            {
                // If there is no theme called "default"...
                if ( angular.isUndefined(service.themes.list.default) )
                {
                    $log.error('You must have at least one theme named "default"');
                    return;
                }

                $log.warn('The theme "' + themeName + '" does not exist! Falling back to the "default" theme.');

                // Otherwise set theme to default theme
                service.themes.active.name = 'default';
                service.themes.active.theme = service.themes.list.default;
                $cookies.put('selectedTheme', service.themes.active.name);

                return;
            }

            service.themes.active.name = themeName;
            service.themes.active.theme = service.themes.list[themeName];
            $cookies.put('selectedTheme', themeName);
        }

        /**
         * Set available themes list
         *
         * @param themeList
         */
        function setThemesList(themeList)
        {
            service.themes.list = themeList;
        }
    }
})();

(function ()
{
    'use strict';

    angular
        .module('app.core')
        .provider('fuseTheming', fuseThemingProvider);

    /** @ngInject */
    function fuseThemingProvider()
    {
        // Inject Cookies Service
        var $cookies;

        angular.injector(['ngCookies']).invoke([
            '$cookies', function (_$cookies)
            {
                $cookies = _$cookies;
            }
        ]);

        // Inject $log service
        var $log = angular.injector(['ng']).get('$log');

        var registeredPalettes,
            registeredThemes;

        // Methods
        this.setRegisteredPalettes = setRegisteredPalettes;
        this.setRegisteredThemes = setRegisteredThemes;

        //////////

        /**
         * Set registered palettes
         *
         * @param _registeredPalettes
         */
        function setRegisteredPalettes(_registeredPalettes)
        {
            registeredPalettes = _registeredPalettes;
        }

        /**
         * Set registered themes
         *
         * @param _registeredThemes
         */
        function setRegisteredThemes(_registeredThemes)
        {
            registeredThemes = _registeredThemes;
        }

        /**
         * Service
         */
        this.$get = function ()
        {
            var service = {
                getRegisteredPalettes: getRegisteredPalettes,
                getRegisteredThemes  : getRegisteredThemes,
                setActiveTheme       : setActiveTheme,
                setThemesList        : setThemesList,
                themes               : {
                    list  : {},
                    active: {
                        'name' : '',
                        'theme': {}
                    }
                }
            };

            return service;

            //////////

            /**
             * Get registered palettes
             *
             * @returns {*}
             */
            function getRegisteredPalettes()
            {
                return registeredPalettes;
            }

            /**
             * Get registered themes
             *
             * @returns {*}
             */
            function getRegisteredThemes()
            {
                return registeredThemes;
            }

            /**
             * Set active theme
             *
             * @param themeName
             */
            function setActiveTheme(themeName)
            {
                // If theme does not exist, fallback to the default theme
                if ( angular.isUndefined(service.themes.list[themeName]) )
                {
                    // If there is no theme called "default"...
                    if ( angular.isUndefined(service.themes.list.default) )
                    {
                        $log.error('You must have at least one theme named "default"');
                        return;
                    }

                    $log.warn('The theme "' + themeName + '" does not exist! Falling back to the "default" theme.');

                    // Otherwise set theme to default theme
                    service.themes.active.name = 'default';
                    service.themes.active.theme = service.themes.list.default;
                    $cookies.put('selectedTheme', service.themes.active.name);

                    return;
                }

                service.themes.active.name = themeName;
                service.themes.active.theme = service.themes.list[themeName];
                $cookies.put('selectedTheme', themeName);
            }

            /**
             * Set available themes list
             *
             * @param themeList
             */
            function setThemesList(themeList)
            {
                service.themes.list = themeList;
            }
        };
    }
})();

(function ()
{
    'use strict';

    angular
        .module('app.core')
        .config(config);

    /** @ngInject */
    function config($mdThemingProvider, fusePalettes, fuseThemes, fuseThemingProvider)
    {
        // Inject Cookies Service
        var $cookies;
        angular.injector(['ngCookies']).invoke([
            '$cookies', function (_$cookies)
            {
                $cookies = _$cookies;
            }
        ]);

        // Check if custom theme exist in cookies
        var customTheme = $cookies.getObject('customTheme');
        if ( customTheme )
        {
            fuseThemes['custom'] = customTheme;
        }

        $mdThemingProvider.alwaysWatchTheme(true);

        // Define custom palettes
        angular.forEach(fusePalettes, function (palette)
        {
            $mdThemingProvider.definePalette(palette.name, palette.options);
        });

        // Register custom themes
        angular.forEach(fuseThemes, function (theme, themeName)
        {
            $mdThemingProvider.theme(themeName)
                .primaryPalette(theme.primary.name, theme.primary.hues)
                .accentPalette(theme.accent.name, theme.accent.hues)
                .warnPalette(theme.warn.name, theme.warn.hues)
                .backgroundPalette(theme.background.name, theme.background.hues);
        });

        // Store generated PALETTES and THEMES objects from $mdThemingProvider
        // in our custom provider, so we can inject them into other areas
        fuseThemingProvider.setRegisteredPalettes($mdThemingProvider._PALETTES);
        fuseThemingProvider.setRegisteredThemes($mdThemingProvider._THEMES);
    }

})();
